import React, { useState } from 'react';
import { useCookies } from 'react-cookie';

import './CookieConsent.scss'

const CookieConsent = () => {
    const [cookies, setCookie] = useCookies(["cookieConsent"]);

    const [showCookie, setShowCookie] = useState(true)


    const giveCookieConsent = () => {
        setCookie("cookieConsent", true, { path: "/" });

        setShowCookie(false)
    };

    return (
        <div className={showCookie ? "cookie_container" : "cookie_container hide"}>
            <p>
                Ta strona wykorzystuje pliki cookies w celu zapewnienia prawidowego dziaania poszczeglnych jej funkcji (pliki cookies wasne) oraz pliki cookies pochodzce od podmiotw trzecich w celu korzystania z narzdzi zewntrznych (Google Analytics, HotJar, YouTube, Spreaker, MailerLite). Do informacji, ktre s gromadzone w plikach cookies od podmiotw trzecich, maj dostp dostawcy wymienionych narzdzi zewntrznych. Dowiedz si wicej: polityka prywatnoci.{" "}
                <a href={"/privacy-policy"}>Learn more.</a>
            </p>
            <button onClick={giveCookieConsent}>
                Accept
            </button>
        </div>
    );
};

export default CookieConsent;